import { Storage } from "@ionic/storage";
import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { NavController, LoadingController } from "ionic-angular";


@Component({
  selector: "page-prayers",
  templateUrl: "prayers.html"
})
export class PrayersPage {
  email: string;
  name: string;
  phone: string;
  desc: string;
  counseling: boolean;

  constructor(
    public navCtrl: NavController,
    public loadingCtrl: LoadingController,
    public http: HttpClient,
    private storage: Storage
  ) {
    this.storage.get("user").then(user => {
      if (user !== null) {
        this.email = user.email;
        this.name = user.name;
        this.phone = user.phone || "";
      }
    });
  }

  onSubmit() {
    let load = this.loadingCtrl.create({
      content: "Submitting form...",
      spinner: "dots"
    });
    load.present();

    this.http
      .post("https://realm-glory.herokuapp.com/api/prayers", {
        email: this.email,
        name: this.name,
        phone: this.phone || "",
        message: this.desc,
        counseling: this.counseling
      })
      .subscribe(
        status => {
          load.dismiss();
          if (status["status"] === "success") {
            alert(
              "We will be praying for you, also a member of staff would reach out to you."
            );
            this.navCtrl.pop();
          } else {
            alert("An error occurred, please try again.");
          }
        },
        err => {
          load.dismiss();
          console.log("err", err);
        }
      );
  }
}



